import React, { useState } from 'react';
import axios from 'axios';
import './App.css';

const App = () => {
  const [lastOrdered, setLastOrdered] = useState(new Date().toLocaleDateString());
  const [showPopup, setShowPopup] = useState(false);
  const [selectedItem, setSelectedItem] = useState(null);

  const promptForWebhookURL = () => {
    let webhookURL = localStorage.getItem('webhookURL');
  
    if (!webhookURL) {
      webhookURL = prompt('Please enter your Discord webhook URL:');
      localStorage.setItem('webhookURL', webhookURL);
    }
  
    return webhookURL;
  };

  const clearWebhookURL = () => {
    localStorage.removeItem('webhookURL');
    window.location.reload();
  };  

  const webhookURL = promptForWebhookURL();

  const foodItems = [
    { id: 1, title: 'Almond Butter', image: 'almond-butter.jpg' },
    { id: 2, title: 'Blueberries', image: 'blueberries.jpg' },
    { id: 3, title: 'Broccoli', image: '/images/broccoli.jpg' },
    { id: 4, title: 'Bananas', image: './images/bananas.jpg' },
    { id: 5, title: 'Eggs', image: './images/eggs.jpg' },
    { id: 6, title: 'Apples', image: './images/apples.jpg' },
    { id: 7, title: 'Avocado', image: './images/avocado.jpg' },
    { id: 8, title: 'Bread', image: './images/bread.jpg' },
    { id: 9, title: 'Carrots', image: './images/carrots.jpg' },
    { id: 10, title: 'Tofu', image: './images/tofu.jpg' },
    { id: 11, title: 'Potato', image: './images/potato.jpg' },
    { id: 12, title: 'Coffee', image: './images/coffee.jpg' },
    { id: 13, title: 'Zucchini', image: './images/zucchini.jpg' },
    { id: 14, title: 'Oatly', image: './images/oatly.jpg' },
    { id: 15, title: 'Dark Chocolate', image: './images/dark-chocolate.jpg' },
    { id: 16, title: 'Butter', image: './images/butter.jpg' },
    { id: 17, title: 'Granola', image: './images/granola.jpg' },
    { id: 18, title: 'Grapes', image: './images/grapes.jpg' },
    { id: 19, title: 'Green Beans', image: './images/green-beans.jpg' },
    { id: 20, title: 'Honey', image: './images/honey.jpg' },
    { id: 21, title: 'Ice Cream', image: './images/ice-cream.jpg' },
    { id: 22, title: 'Lemon', image: './images/lemon.jpg' },
    { id: 23, title: 'Lettuce', image: './images/lettuce.jpg' },
    { id: 24, title: 'Milk', image: './images/milk.jpg' }
  ];

  const handleOrder = item => {
    setSelectedItem(item);
    setShowPopup(true);
  };

  const handleConfirm = async () => {
    const discordUrl = webhookURL
    try {
      await axios.post(discordUrl, { content: `${selectedItem.title}` });
      setLastOrdered(new Date().toLocaleDateString());
    } catch (error) {
      console.error(error);
    }
    console.log(selectedItem.title);
    setShowPopup(false);
  };
  
  const handleCancel = () => {
    setShowPopup(false);
  };
  
  return (
    <>
      <div>
       
      <div className="grid">
        {foodItems.map(item => (
          <div key={item.id} className="card" onClick={() => handleOrder(item)}>
            <img src={`/src/assets/images/${item.image}`} alt={item.title} />
            <h2>{item.title}</h2>
            <p>Last Ordered: {lastOrdered}</p>
          </div>
        ))}
      </div>
      {showPopup && (
        <div className="popup">
          <p>Are you sure you want to order {selectedItem.title}?</p>
          <button onClick={handleConfirm}>Yes</button>
          <button onClick={handleCancel}>No</button>
        </div>
      )}
      <button className='clearWebhook' onClick={clearWebhookURL}>Clear Webhook URL</button>
      </div>
    </>
  );  
};

export default App;